import Router from 'preact-router';
import { Component, h, render as preactRender } from 'preact';
import { LineGraph } from './feature-line-graph/line-graph';
import { createState, loadState, saveState } from './state';
import { selectData, fetchData } from './data';
import { log } from './utility';
import { LearningTable } from './feature-learning-table/learning-table';
import { Header } from './components/header';
import { fullSize, flexCol } from './constants';
export class App extends Component {
    constructor() {
        super();
        let state = loadState();
        if (!state) {
            log('No existing state');
            state = createState();
        }
        this.state = state;
        this.selectAndUpdate();
    }
    updateSelectState({ countries, series, }) {
        this.setState(Object.assign(Object.assign({}, this.state), { countries, currentSeries: series }));
    }
    selectAndUpdate() {
        this.state.dataPromise
            .then(({ timeSeries }) => {
            this.setState(Object.assign(Object.assign({}, this.state), { data: timeSeries }));
            return selectData(this.props.cache, this.state);
        })
            .then(this.updateSelectState.bind(this))
            .then(() => saveState(this.state));
    }
    lineGraphState(lgs) {
        this.setState(Object.assign(Object.assign({}, this.state), { lineGraphState: lgs }));
        this.selectAndUpdate();
    }
    tableState(tableState) {
        this.setState(Object.assign(Object.assign({}, this.state), { tableState }));
        this.selectAndUpdate();
    }
    reload() {
        this.props.reset();
        this.setState({
            dataPromise: fetchData().then(d => {
                this.selectAndUpdate();
                return d;
            }),
        });
    }
    selectCountry(countryIndex) {
        if (this.state.lineGraphState.countryIndexes.indexOf(countryIndex) > -1) {
            this.setState(Object.assign(Object.assign({}, this.state), { lineGraphState: Object.assign(Object.assign({}, this.state.lineGraphState), { countryIndexes: this.state.lineGraphState.countryIndexes.filter(i => i !== countryIndex) }) }));
        }
        else {
            this.setState(Object.assign(Object.assign({}, this.state), { lineGraphState: Object.assign(Object.assign({}, this.state.lineGraphState), { countryIndexes: this.state.lineGraphState.countryIndexes.concat([
                        countryIndex,
                    ]) }) }));
        }
        this.selectAndUpdate();
    }
    render() {
        return (h("div", { className: `${fullSize} ${flexCol}` },
            h(Header, null),
            h(Router, null,
                h(LineGraph, { path: '/', countries: this.state.countries, currentSeries: this.state.currentSeries, onChange: this.lineGraphState.bind(this), key: "0", reload: this.reload.bind(this), state: this.state.lineGraphState }),
                h(LearningTable, { countryIndexes: this.state.lineGraphState.countryIndexes, key: "1", onChange: this.tableState.bind(this), path: '/table', state: this.state.tableState, selectCountry: this.selectCountry.bind(this), timeSeries: this.state.data }))));
    }
}
export function render(root) {
    let cache = {};
    preactRender(h(App, { cache: cache, reset: () => (cache = {}) }), root);
}
